<style type="text/css">

	a{
		color: inherit;
		text-decoration: none;
	}
	
	.navbar{
		width: 100%;
		height: auto;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		z-index: 9;
		top: 0px;
		left: 0px;
		position: fixed;
		background: white;
	}
	.navbar .left{
		width: 70%;
		height: 60px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}
	.navbar .left .menu{
		width: 80%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}
	.navbar .left .menu ul{
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: space-around;
		align-items: center;
		flex-wrap: wrap;
		margin: 0px;
		padding: 0px;
	}
	.navbar .left .menu ul li{
		list-style: none;
		font-family: varela;
		cursor: pointer;
		color: gray;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		height: 70%;
		font-size: 14px;
		position: relative;
	}

	.navbar .left .logo{
		width: 20%;
		height: 100%;
		justify-content: space-around;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
	}
	.navbar .left .logo .brand img{
		width: 50px;
		height: 40px;
	}
	.navbar .left .logo .brand{
		width: 200px;
		height: auto;
			display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: center;
	}
	.navbar .left .logo .brand b{
		font-family: 'Fuggles', cursive;
		font-size: 40px;
		margin-left: 10px;
	}
	.navbar .right{
		width: 30%;
		height: 60px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}
	.navbar .right input{
		margin-right: 20px;
		width: 200px;
		height: 60%;
		outline: none;
		border: none;
		padding: 5px;
		margin-top: 10px;
		background: #FFFFFF;
		border-radius: 8px;
		text-align: center;
	}
	.navbar .right button{
		width: 100px;
		height: 60%;
		margin-top: 10px;
		background: linear-gradient(121.32deg, #490C8A -0.1%, #CB1671 111.03%);
		border-radius: 5px;
		outline: none;
		color: white;
		font-family: poppins;
		border: none;
		cursor: pointer;
		font-weight: 600;
		font-size: 12px;
	}
	.selected{
		position: relative;
		font-weight: bold;
		color: black !important;
	}
	.selected2{
		font-weight: bold !important;
		color: black !important;
	}
	.selected:after{
		content: "";
		width: 70%;
		height: 3px;
		border-radius: 5px;
		bottom: 0px;
		background: linear-gradient(93.4deg, #490C8A 5.95%, #4676D7 94.66%);
		position: absolute;
		transition: 0.7s all;
	}
	.navbar .left .menu-res{
		outline: none;
		border: none;
		background: none;
		height: 100%;
		font-size: 30px;
		display: none;
		color: #490C8A;
		cursor: pointer;
	}
	.navbar .left .login-btn{
		width: 70px;
		height: 60%;
		background: linear-gradient(121.32deg, #490C8A -0.1%, #CB1671 111.03%);
		border-radius: 5px;
		outline: none;
		color: white;
		font-family: poppins;
		border: none;
		cursor: pointer;
		font-weight: 600;
		font-size: 12px;
		display: none;
	}
	.menu-responsive{
		width: 0px;
		height: 100%;
		position: fixed;
		left: 0px;
		top: 0px;
		z-index: 10;
		background: white;
		box-shadow: 0px 0px 30px rgba(0,0,0,0.1);
		display: flex;
		justify-content: center;
		align-items: flex-start;
		flex-direction: column;
		overflow-x: hidden;
		overflow-y: auto;
		transition: 0.5s width;
	}

	.menu-responsive-active{
		width: 300px;
		/*transition: 1s width;*/
	}

	.menu-responsive .menu{
		width: 300px;
		height: auto;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}

	.menu-responsive .menu ul{
		margin: 0px;
		width: 80%;
		padding-top: 0px;
		padding: 20px;
	}

	.menu-responsive .menu ul li{
		margin: 5px;
		padding: 10px 0px;
		cursor: pointer;
		list-style: none;
		color: gray;
	}

	.menu-responsive .action{
		width: 300px;
		padding: 10px;
		height: auto;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}

	.menu-responsive .action input{
		width: 80%;
		height: 50px;
		outline: none;
		border: none;
		background: #F5F5FF;
		padding: 10px;
		border-radius: 100px;
		text-align: center;
	}

	.menu-responsive .logo{
		width: 300px;
		height: 150px;
		justify-content: center;
		align-items: center;
		display: flex;
		flex-wrap: wrap;
	}

	.menu-responsive .logo img{
		width: 130px;
		height: 100px;
	}

	.menu-responsive .close{
		width: 300px;
		height: auto;
		display: flex;
		justify-content: flex-end;
		flex-wrap: wrap;
		align-items: center;
		padding:10px 20px;
	}

	.menu-responsive .close i{
		font-size: 25px;
		cursor: pointer;
	}

	.menu-responsive .action button{
		margin-top: 10px;
		width: 80%;
		height: 50px;
		background: linear-gradient(93.4deg, #490C8A 5.95%, #4676D7 94.66%);
		border-radius: 100px;
		outline: none;
		color: white;
		font-family: poppins;
		border: none;
		cursor: pointer;
		font-weight: 600;
		font-size: 12px;
	}

	.bg-menu-responsive{
		width: 100%;
		height: 100%;
		position: fixed;
		background: rgba(0,0,0,0.50);
		top: 0px;
		left: 0px;
		z-index: 9;
	}

	@media screen and (min-width: 320px) and (max-width: 1100px){
		
		.navbar{
			top: 0px;
			position: fixed;
		}
		.navbar .left .logo{
			width: 100%;
			justify-content: space-between;
			padding-right: 20px;
		}
		.navbar .left .menu-res{
			font-size: 20px;
		}
		.navbar .left .menu-res,.navbar .left .login-btn{
			display: block;
		}
		.navbar .left{
			width: 100%;
		}
		.navbar .left .menu{
			display: none;
		}
		.navbar .right{
			display: none;
		}
	}
	
</style>

<script type="text/javascript">
	
	import {navbarResponsiveShow} from "./store.js";
	import AboutBook from "./AboutBook.svelte";
	import {onMount} from "svelte";

	let navbarChild = false;

	let showNavbar = ()=> $navbarResponsiveShow = !$navbarResponsiveShow;

	onMount(()=>{

		let menu = document.querySelector("#menu-bars");
		let menuChild = menu.querySelectorAll("li");

		menuChild.forEach((e,index) =>{

			e.onclick = ()=>{

				menuChild[index].classList.add("selected");

				if(index === 2) navbarChild = true;
				else navbarChild = false;

				for(let x = 0; x < menuChild.length; x++){

					if(x !== index){
						menuChild[x].classList.remove("selected");
					}

				}			

			}

		});

		let menu2 = document.querySelector("#o-menu");
		let menuChild2 = menu2.querySelectorAll("li");

		menuChild2.forEach((e,index) =>{

			e.onclick = ()=>{

				menuChild2[index].classList.add("selected2");

				if(index === 2) navbarChild = true;
				else navbarChild = false;

				for(let x = 0; x < menuChild2.length; x++){

					if(x !== index){
						menuChild2[x].classList.remove("selected2");
					}

				}			

			}

		});

	})

	let selected = 0;

</script>

<div class="navbar">
	<div class="left">
		
		<div class="logo">
			<button class="menu-res" on:click={showNavbar}>
			<i class="fas fa-bars"></i>
			</button>
			<div class="brand">
				<img src="/res/logo.png">
				<b>alfannani</b>
			</div>
			<button class="login-btn">Login</button>
		</div>
		<div class="menu">
			<ul id="menu-bars">
				<li class="selected">home</li>
				<li>paket penerbitan</li>
				<li>tentang buku</li>
				<li><a href="/#/article">artikel</a></li>
				<li>marketplace</li>
				<li>FAQ</li>
			</ul>
		</div>
	</div>
	<div class="right">
		<input type="search" name="" placeholder="search...">
		<button>Login</button>
	</div>
</div>

{#if $navbarResponsiveShow}
	<div class="bg-menu-responsive"></div>	
{/if}

<div class="menu-responsive" class:menu-responsive-active={$navbarResponsiveShow}>
	<div class="close">
		<i class="fas fa-times" on:click={showNavbar}></i>
	</div>
	<div class="logo">
		<img src="/res/logo.png">
	</div>
	<div class="action">
		<input type="search" name="" placeholder="search..">
		<button>Search</button>
	</div>
	<div class="menu">
		<ul id="o-menu">
			<li class="selected2">home</li>
			<li>paket penerbitan</li>
			<li>tentang buku</li>
			<li>artikel</li>
			<li>marketplace</li>
			<li>FAQ</li>
		</ul>
	</div>
</div>
{#if navbarChild}
	<AboutBook></AboutBook>
{/if}